import { RouterInterface } from '@/routers/router-interface';
import { NylasAuthType } from '@nylas/core';
import { NylasAuthStoreType, NylasMailboxStoreType, NylasSchedulerStoreType } from '..';
import { NylasAuthConnector } from './shared/api/auth';
import { NylasMessagesConnector } from './shared/api/messages';
import { NylaSchedulerAPIConnector } from './shared/api/scheduler';
import { NylasThreadsConnector } from './shared/api/threads';
import { NylasFoldersConnector } from './shared/api/folders';
import { NylasSchedulerConfigStoreType } from '@/stores/scheduler-config-store';
import { NylasSchedulerConfigAPIConnector } from './shared/api/scheduler-config';

export interface BaseNylasConnectorInterface {
  resetStores(): void;
  init(): void;
}

export interface NylasAuthConnectorInterface extends BaseNylasConnectorInterface {
  nylasAuth: NylasAuthType;
  nylasAuthStore: NylasAuthStoreType;
  get authStore(): NylasAuthStoreType;
  get auth(): NylasAuthConnector;
  logout(): Promise<void>;
}

export interface NylasMailboxConnectorInterface extends NylasAuthConnectorInterface {
  nylasMailboxStore: NylasMailboxStoreType;
  router: RouterInterface;
  get mailboxStore(): NylasMailboxStoreType;
  get threads(): NylasThreadsConnector;
  get messages(): NylasMessagesConnector;
  get folders(): NylasFoldersConnector;
}

export interface NylasSchedulerConnectorInterface extends BaseNylasConnectorInterface {
  schedulerAPIURL: string;
  nylasSchedulerStore: NylasSchedulerStoreType;
  get schedulerStore(): NylasSchedulerStoreType;
  get scheduler(): NylaSchedulerAPIConnector;
}

export interface NylasSchedulerConfigConnectorInterface extends BaseNylasConnectorInterface {
  nylasSchedulerConfigStore: NylasSchedulerConfigStoreType;
  get schedulerConfigStore(): NylasSchedulerConfigStoreType;
  get schedulerConfig(): NylasSchedulerConfigAPIConnector;
}

export interface NylasConnectorInterface extends NylasAuthConnectorInterface, NylasMailboxConnectorInterface, NylasSchedulerConnectorInterface {}
